body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #4aaec0;
  background-image: url("./img/blue.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul {
  list-style: none;
  margin-left: 0;
}

.list {
  list-style: none;
  display: flex;
  margin-right: 20px;
}

.right {
  justify-content: flex-end;
}

.listItem {
  margin-right: 20px;
}
.listItem:last-child {
  margin-right: 0;
}

.link {
  text-decoration: none;
}

.activeLink {
  color: cornflowerblue;
}

button {
  background-color: #819ff5;
  color: #ffffff;
  height: 30px;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  border: none;
  border-radius: 50pc;
  outline: none;
  cursor: pointer;
  margin-top: 10px;
}

label {
  font-size: 12px;
  line-height: 20px;
}

input,
select {
  border: 1px solid #819ff5;
  height: 25px;
  border-radius: 50pc;
  outline: none;
  margin-bottom: 10px;
  width: 100%;
}

form {
  width: 300px;
  margin: 0 auto;
}

.playMusic {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  width: 100%;
  text-align: center;
  margin: 10% 0;
  min-height: 100px;
  align-items: center;
}

#root {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.loader-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
}
